@import url('https://fonts.googleapis.com/css2?family=Manrope&display=swap');

* {
  font-family: 'Manrope', sans-serif;
}

.image-collage-app {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(-20deg, #dcb0ed 0%, #99c99c 100%);
}

.image-collage-welcome,
.image-collage-desc,
.image-collage-btn,
.image-collage-select {
  margin: 0 0 10px;
}

.image-collage-select select {
  background-color: transparent;
  margin-left: 10px;
  padding: 3px;
  font-weight: bold;
  border-radius: 5px;
}

.image-collage-desc {
  width: 75%;
}

.image-collage-btn {
  padding: 4px 10px;
  border: 2px solid #392813;
  color: #392813;
  background-color: transparent;
  border-radius: 15px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
}

.image-collage-app-container {
  display: flex;
  flex-direction: column;
  width: 335px;
  height: 596px;
  background: url('./assets/img/template_scrapbook_2_3_image-x.png');
  background-size: contain;
  position: relative;
  overflow: hidden;
}

.text-container {
  width: 100%;
  height: 200px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.text-container textarea {
  background-color: transparent;
  border: none;
  width: 75%;
  margin: 0 auto;
  text-align: center;
  outline: none;
}

.text-container p {
  color: #775429;
  margin: 0;
  font-weight: bold;
}

.text-container p.editable-p1 {
  font-size: 1.2rem;
}

.text-container p.editable-p2 {
  font-size: 2rem;
  line-height: 1;
}

.image1,
.image2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
}

.image1 {
  background-image: url('./assets/img/template_scrapbook_2_1_image.png');
  left: -15px;
  top: 22.5%;
  z-index: 2;
  width: 235px;
  height: 300px;
}

.image2 {
  background-image: url('./assets/img/template_scrapbook_2_2_image.png');
  bottom: -40px;
  right: -15px;
  z-index: 1;
  width: 220px;
  height: 280px;
}

.image1 input,
.image2 input {
  height: 100%;
  width: 100%;
  opacity: 0;
  position: absolute;
  z-index: 3;
}

.image1 img,
.image2 img {
  width: 172px;
  height: 216px;
  z-index: 1;
}

.image1 img,
.image1 .image-p {
  transform: rotate(10deg);
}

.image2 img,
.image2 .image-p {
  transform: rotate(-4deg);
}